@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,600&display=swap);
.container-heading{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;margin:auto}.container-heading h2{font-weight:300;color:white;font-size:40;margin-bottom:0.6em}

.about{padding-top:1%;padding-left:5%;padding-right:5%;background-color:#000022;font-size:1.5vw}.about-description-container{width:100%;display:flex;align-items:center}.about-text{color:white;font-family:Segoe UI;padding:5%}.brain-image{background-image:url(/static/media/brainImage.3bd1c289.png);background-size:130%;background-repeat:no-repeat;background-position:center;background-position:40% 50%;width:100%;height:100%}@media only screen and (max-width: 768px){.about{font-size:3.5vw}}

.card-about{width:90%;padding-left:5%;padding-right:5%;border-radius:12px;display:flex;flex-direction:column;align-items:center;margin:5% 5% 5% 5%;transition:0.3s;border:1px solid rgba(63,159,190,0.5)}.card-about:hover{box-shadow:0px 0px 20px 6px rgba(63,159,190,0.75)}#cardIcon{margin:3%;width:15%}#cardText{color:white;font-family:Segoe UI;font-size:1.7vw}@media only screen and (max-width: 768px){#cardText{font-size:3.5vw}}

.accordion__section{display:flex;flex-direction:column;border:3px solid #3f9fbe}.accordion{background-color:#002;color:white;cursor:pointer;padding:18px;display:flex;align-items:center;border:none;outline:none;transition:background-color 0.6s ease}.accordion:hover,.active{background-color:#202048}.accordion__title{font-family:"Open Sans", sans-serif;font-weight:600;font-size:14px;text-align:left}.accordion__icon{margin-left:auto;transition:transform 0.6s ease}.accordion__icon path{fill:#3f9fbe}.rotate{transform:rotate(90deg)}.accordion__content{background-color:#002;color:white;overflow:auto;transition:max-height 0.6s ease;overflow-y:hidden;-ms-overflow-style:none;scrollbar-width:none}.accordion__content::-webkit-scrollbar{display:none}.accordion__text{font-family:"Open Sans", sans-serif;font-weight:400;font-size:14px;padding:18px}

.faq{width:100%;background:#002;padding:5%}.faq select{height:3em;margin-bottom:2.5em;border:none}.faq .faq-col-padding{padding-bottom:20px}.accordion{padding:5px 10px 5px 10px}.accordion-title{margin-bottom:0rem !important}

.footer-wrapper{display:grid;grid-gap:10px;grid-template-columns:1fr 1fr 1fr;background-color:#618A93;align-items:center;justify-items:center;width:100%;color:white}.social-media{display:flex}.facebook-icon,.linkedin-icon{color:white;font-size:3vh}.footer-wrapper a{padding-right:15%}.footer-newsletter,.footer-flaticon{grid-column-start:1;grid-column-end:4;width:100%;text-align:center;padding-bottom:0.5%}.footer-newsletter a,.footer-flaticon a{padding-right:0;color:white}.footer-flaticon{font-size:0.7rem}

canvas{background-color:#000022}.landing-hero-page{width:100%;height:100vh;padding-left:5%;color:#ffffff;font-family:'Segoe UI';display:flex;align-items:center;padding-top:3%}.bg-color-hack{background-color:#000022}.content{width:100%;height:100%}.mobile-content{display:none}.title{font-size:3.5vw;text-align:left;margin-bottom:1%;font-family:Trebuchet MS}.subtitle{font-family:Segoe UI;font-size:1.5vw;font-weight:300}.text-container{display:flex;flex-direction:column;justify-content:center}.neural-net-image{background-image:url(/static/media/backgroundImg.ea60373b.png);background-size:80%;background-repeat:no-repeat;background-position:40% 50%}.register{transition:all 0.3s ease 0s;margin-top:5%;padding:3%;background-color:#04cfda;color:white;font-size:2.5vw;border-radius:12px;outline:none;border:none;width:45%}.register a{color:#fff;text-decoration:none}.register:hover{width:50%;background-color:#4A8FA1;transition:all 0.6s ease 0s;cursor:pointer}.scroll-down{opacity:1;transition:all .5s ease-in 3s}.scroll-down{position:absolute;bottom:40px;left:50%;margin-left:-16px;display:block;width:40px;height:40px;border:2px solid #FFF;background-size:14px auto;border-radius:50%;z-index:1;animation:bounce 2s infinite 2s;transition:all .2s ease-in;transform:scale(1)}.scroll-down:before{position:absolute;top:calc(50% - 8px);left:calc(50% - 6px);transform:rotate(-45deg);display:block;width:12px;height:12px;content:"";border:2px solid white;border-width:0px 0 2px 2px}@keyframes bounce{0%,100%,20%,50%,80%{transform:translateY(0)}40%{transform:translateY(-10px)}60%{transform:translateY(-5px)}}@media screen and (max-width: 768px){.landing-hero-page{padding:5%;text-justify:distribute;background-color:none}.neural-net-image{display:none}.title{font-size:5vw}.subtitle{font-size:4vw}.mobile-content{display:flex;width:100%;flex-direction:column;align-items:center;justify-content:center;text-align:center}.register{width:20%;margin-top:0;font-size:3.5vw}.register:hover{width:25%}.content{display:none}.particles{position:fixed;right:0;right:0;bottom:0;left:0;z-index:-1}}@media screen and (max-width: 600px){.neural-net-image{width:130%}}

.schedule{background-color:#002;color:white;font-family:'Segoe UI';min-height:100vh;padding:5%}.schedule h5{text-align:center}.schedule .nav{border:none;font-size:1.75vw;padding-bottom:0.5%}.schedule .nav-item{cursor:pointer}.schedule .nav-link{color:white}.schedule .nav-tabs{margin:auto;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.schedule .tab-pane{width:50%;margin:auto}.schedule .tab-pane .container{background-color:#002}.schedule .event{border-top:1px solid white;font-size:1.5vw}.schedule .event:nth-child(1){border-top:0px solid white}.schedule .nav-item.active{background-color:#3f9fbe}@media screen and (max-width: 1200px){.schedule .nav{font-size:2.5vw}.schedule .event{font-size:2.0vw}}@media screen and (max-width: 768px){.schedule .nav{font-size:3.5vw}.schedule .event{font-size:3vw}}@media screen and (max-width: 500px){.schedule .nav{font-size:4.5vw}.schedule .event{font-size:4vw}}

.speaker{height:300px;width:300px;background:#002}.speaker .avatar{height:200px;width:200px;position:relative;left:20%}.speaker .avatar .circle-dark{fill:#045477}.speaker .avatar .circle-light{fill:#04cfda}.speaker .avatar img{width:90%;height:90%;border-radius:50%}.speaker .avatar>*{position:absolute}.speaker .text{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;color:white;text-align:center;width:100%}.speaker .text :nth-child(1){font-size:20px}.speaker .text:nth-child(2){font-size:18px}.speaker .text:nth-child(3){font-size:16px}

.speakers{background-color:#002;width:100%;padding:5%;display:flex;flex-direction:column;justify-content:center}.speakers .speaker-wrapper{margin:auto;display:flex;width:-webkit-min-content;width:min-content;padding-top:10%;padding-bottom:8px}.speakers .active{background-color:#002}.speakers h6{color:white;text-align:center}

.partners-section{padding-top:5rem;padding-bottom:5rem;background:#618a93;background:linear-gradient(0deg, #618A93 0%, #002 100%)}.parters-center,.center-logos{display:flex;align-items:center;justify-content:center;padding-bottom:20px}.partners-logos-container{padding-top:10px;width:90%}.center-logos{padding-bottom:0}.sponsor-us-btn{float:center;transition:all 0.3s ease 0s;padding:1%;background-color:#6d7879;color:white;font-size:1.5vw;border-radius:12px;outline:none;border:none;width:15%}.sponsor-us-btn a{color:#fff;text-decoration:none}.sponsor-us-btn:hover{width:12%;background-color:#4A8FA1;transition:all 0.6s ease 0s;cursor:pointer}@media screen and (max-width: 768px){.sponsor-us-btn{font-size:2.5vw;width:20%}.sponsor-us-btn:hover{width:18%}}@media screen and (max-width: 600px){.sponsor-us-btn{font-size:4vw;width:30%}.sponsor-us-btn:hover{width:28%}}

.nav-container{background-color:#000022;width:100%;position:fixed;padding-bottom:1%;z-index:2;box-shadow:0}.scroll{box-shadow:0 2px 2px #04cfda62}.nav-row{max-width:1140px;background-color:#000022;margin:0 auto}.main-nav{float:right;list-style:none}.main-nav li{display:inline-block;margin-left:40px;margin-top:25px}.a{text-transform:uppercase;color:#fff;cursor:pointer}.main-nav li .a:link,.main-nav li .a:visited{color:#ffffff;font-size:18px;border-bottom:2px solid transparent;transition:border-bottom 0.2s}.main-nav li .a:hover,.main-nav li .a:active{padding-bottom:5px;border-bottom:2px solid #04cfda;color:#fff;text-decoration:none}.nav-logo{height:40px;float:left;margin-top:1.5%}.mobile-nav-icon{float:right;margin-top:30px;cursor:pointer;color:white;font-size:140%;display:none;right:0;top:0}.navLink{font-family:Segoe UI;color:white;font-size:25px;margin-right:4%}.navLink:hover{cursor:pointer}@media only screen and (max-width: 1200px){.main-nav{margin-right:30px}}@media only screen and (max-width: 1000px){.main-nav li{margin-left:10px}}@media only screen and (max-width: 767px){.main-nav li{margin-left:40px}.main-nav li .a:hover,.main-nav li .a:active{border:0}.main-nav{float:left;text-align:left;padding-left:0;margin-left:-25px;margin-top:20px;display:none}.show{display:block}.main-nav li{display:block;margin-top:5px}.sticky .main-nav li{margin-top:5px}.sticky .main-nav{margin-top:15px}.mobile-nav-icon{position:absolute;display:inline-block;margin-right:10%;margin-top:25px}.main-nav li .a:link,.main-nav li .a:visited{display:block;border:0}}

